<template>
  <div class="idMsg">
    <van-nav-bar title="账号管理" left-text="返回" @click-left="goBack" />
    <van-cell title="头像" class="photo">
      <div class="img-box fr">
        <img class="auto-img v-img" :src="userData.userImg" @oversize="oversize"/>
        <van-uploader :max-size="maxSize" :after-read="afterRead" class="up-img" @oversize="oversize" />
      </div>
    </van-cell>
    <van-cell class="nickName" title="昵称">
      <input type="text" v-model="userData.nickName" @blur="blur({url:'/updateNickName',key:'nickName',value:userData.nickName})" />
    </van-cell>
    <van-cell title="用户 ID" :value="userData.userId" />
    <van-cell title="手机号" :value="userData.phone" />
    <van-cell title="简介" class="v-box">
      <textarea rows="2" maxlength="35" class="v-text" v-model="userData.desc" @blur="blur({url:'/updateDesc',key:'desc',value:userData.desc})"></textarea>
    </van-cell>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userData: {},
    //  头像最大上传大小
      maxSize: 1024 * 500
    };
  },
  created() {
    this.getUserData();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 上传头像
    afterRead(file) {
    //   
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      // 此时可以自行将文件上传至服务器
      // 
      // 获取base64格式图片
      let imgData = file.content.split(",")[1];
      let type = file.file.type.split("/")[1];
      // 
      // 
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          imgType: type,
          serverBase64Img: imgData
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'H001') {
            this.userData.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });

    },
    // 获取用户数据
    getUserData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "B001") {
            this.userData = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 修改昵称 / 简介
    blur(obj) {
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: obj.url,
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          [obj.key]: obj.value
        }
      })
        .then(result => {
          this.$toast.clear();
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    // 上传图片超出大小提示
    oversize() {
      this.$toast("图片大小不能超过500k哦");
    }
  }
};
</script>

<style lang="less" scoped>
.idMsg {
  /deep/ .van-nav-bar__text {
    color: slateblue;
    border-left: 1px dashed #eee;
    border-top: 1px dashed #eee;
    border-radius: 13px;
    padding: 2px 8px;
    box-shadow: 2px 2px 2px #ddd;
    line-height: 25px;
  }
  /deep/.van-cell__value {
    flex: 4;
  }
  .photo {
    height: 80px;
    line-height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;
    .img-box {
      height: 60px;
      width: 60px;
      .v-img {
        width: 60px;
        height: 60px;
        position: absolute;
        top: 0;
        right: 0;
        border-radius: 10px;
        overflow: hidden;
      }
      .up-img {
        position: absolute;
        opacity: 0;
        top: 0;
        right: 0;
        padding: 0;
        margin: 0;
      }
    }
  }
  .v-box {
    height: 80px;
    .v-text {
      width: 100%;
      resize: none;
      padding: 5px;
      box-sizing: border-box;
      margin: 0;
      border: none;
      outline: none;
    }
  }
  .nickName {
    input {
      border: none;
      outline: none;
      padding: 0;
      margin: 0;
      text-align: right;
    }
  }
}
</style>